body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  zoom: 150%;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.button {
  font-family: "Berlin Sans FB";
  position: relative;
  background-color: rgba(255, 255, 255, 0.2);
  border: none;
  color: orange;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 12px;
  padding: 8px;
  margin-bottom: 50px;
  margin-left: 9.5%;
  -webkit-transition-duration: 0.2s; /* Safari */
  transition-duration: 0.2s;
  cursor: pointer;
  border: 1px solid grey;
  border-radius: 50%;
}

.button:hover {
  background-color: orange;
    border: 1px solid orange;
    color: grey;
}

.button:active {
  background-color: #ff8c00;
    border: 1px solid #ff8c00;
  color: grey;
}

.button:focus {
  outline: 0;
  box-shadow: none;
}

.todos {
    text-align: center;
}

.todo-wrapper, .historyTodo, .addTodo {
    text-align: left;
    font-family: "Berlin Sans FB";
    font-size: 12px;
    padding: 8px;
    border: 1px solid lightgrey;
    border-radius: 20px;
    margin-bottom: 10px;
    display: inline-block;
    min-width: 80%;
    -webkit-transition-duration: 0.2s; /* Safari */
    transition-duration: 0.2s;
}

.blue-text {
    text-align: center;
    font-size: 40px;
    font-family: "Berlin Sans FB";
    color: #00b7ff;
}

.history, .noTodo {
    font-family: "Berlin Sans FB";
}

.todo:hover {
    -webkit-text-decoration: black line-through;
            text-decoration: black line-through;
}

.historyTodo {
    -webkit-text-decoration: grey line-through;
            text-decoration: grey line-through;
    color: lightgrey;
}

.addTodo {
    border: 1px solid orange;
}

.addTodo:focus {
    border: 1px solid orangered;
}

.todo-wrapper {
    -webkit-transition-duration: 0.2s; /* Safari */
    transition-duration: 0.2s;
}

.todo-wrapper:hover {
    background-color: #f8f8f8;
}

.todo-wrapper:active {
    background-color: #f3f3f3;
}

.addTodo {
    margin-top: 40px;
}

.addTodo:focus {
    outline: 0;
    box-shadow: none;
}

.by-whom {
  position: relative;
  margin-top: 50px;
  margin-left: 10%;
  font-family: "Berlin Sans FB";
}

a {
  color: orange;
  font-weight: bold;
}

.comma {
  color: orange;
}
